body, html {
    font-family: "source-sans-pro", sans-serif;
    font-size: 16px;
}

.text-area {
    background-color: var(--secondary-background-color);
    border-radius: 8px;
    padding: 16px;
    text-wrap: nowrap;
    overflow-x: auto;
}
.plain-text {
    color: var(--text-color);
}

/* These classes are used to style an annotation */
.annotation {
    display: inline-flex;
    white-space: nowrap;
    overflow: hidden;
    border-radius: 16px;
}
.annotation-text {
    color: rgb(255, 255, 255);
    margin: 0px 8px 0px 8px;
    /* padding: 0px 8px 0px 8px; */
    font-size: 14px;
    text-shadow: 0 0 2px black;
}
.annotation-info {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 4px;
    vertical-align: middle;
    color: rgb(201, 201, 201);
    /* padding: 0px 2px 0px 2px; */
    font-size: 12px;
    text-shadow: 0 0 2px black;
    /* background-color: rgb(167, 167, 167) !important; */
}

.annotation-entity-type {

}
.annotation-confidence {

}
.annotation-delete {
    display: flex;
    align-items: center;
    margin-right: 4px;
}
.annotation-delete:hover {
    cursor: pointer;
    filter:brightness(0.8);
}

/* If the user selects some text this menu will open */
.menu {
    position: absolute;
    display: flex;
    height: 40px;
}
.annotate-dropdown {
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;

    padding: 8px 2rem 8px 8px;
    background-color: white;
    border: 1px solid var(--background-color);
    border-radius: 8px 0px 0px 8px;
    background-image: url("assets/arrow.svg");
    background-repeat: no-repeat;
    background-position: right 0.5rem center;
    background-size: 0.6rem;
}
.annotate-dropdown:focus {
    outline:none;
    border: 1px solid var(--primary-color);
}
option {
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;

    background-color: var(--background-color);
    color: var(--text-color);
}
.annotate-button {
    padding: 0px 11px 0px 10px;
    border-radius: 0px 8px 8px 0px;
    background-color: var(--primary-color);
    border: 1px solid var(--primary-color);
    color: white;
    border: none;
}
.annotate-button:hover:not(:active) {
    filter: brightness(0.96)
}
.annotate-button:active {
    background-color: var(--background-color);
    color: var(--primary-color);
    border: 1px solid var(--primary-color);
}
.annotate-button:focus {
    outline:none;
}